.formulario-carta {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px;
    width: 100%;
    max-width: 600px; /* Limita el ancho máximo del formulario */
    margin: 0 auto;
}
  
.formulario-carta input,
  .formulario-carta textarea {
    width: 100%;
    max-width: 550px; /* Limita el ancho de los inputs */
    padding: 14px;
    font-size: 16px;
    border-radius: 8px;
    border: 2px solid #00471b;
    outline: none;
    background-color: #f4f4f4;
}
  
.formulario-carta textarea {
    height: 150px; /* Altura para el área de texto */
    resize: vertical; /* Permite redimensionar solo en vertical */
}
  
.formulario-carta button {
    padding: 12px 24px;
    font-size: 18px;
    font-family: 'Quicksand', serif;
    background-color: #00471b;
    color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    text-transform: capitalize;
    transition: background-color 0.3s ease;
}
  
.formulario-carta button:hover {
    background-color: #ff6600;
}